// app/login/page.js
'use client'; // <--- Make this a Client Component

import React, { useState, useTransition } from 'react';
import { loginUser } from '../actions/actions'; // Adjust path if actions.js is elsewhere (e.g., ../../actions)
import { useRouter } from 'next/navigation'; // To redirect on success

export default function LoginPage() {
  const [message, setMessage] = useState('');
  const [isPending, startTransition] = useTransition();
  const router = useRouter(); // Initialize router

  const handleSubmit = async (event) => {
    event.preventDefault();
    setMessage(''); // Clear previous messages

    const formData = new FormData(event.currentTarget);

     if (!formData.get('username') || !formData.get('password')) {
         setMessage('Please fill in all fields.');
         return;
     }

    startTransition(async () => {
      const result = await loginUser(formData);
      setMessage(result.message);

      if (result.success) {
        // Login successful - handle session/redirect
        // In a real app, you'd likely set a cookie or token here
        console.log('Logged in user:', result.user?.username); // Use optional chaining
        // Redirect to a dashboard or home page
        router.push('/'); // Redirect to home page after successful login
        // Optionally clear the form:
        // event.target.reset();
      }
    });
  };

  return (
    <div>
      <h1>Login</h1>
      <form onSubmit={handleSubmit}>
        <div>
          <label htmlFor="username">Username:</label>
          <input type="text" id="username" name="username" required />
        </div>
        <div>
          <label htmlFor="password">Password:</label>
          <input type="password" id="password" name="password" required />
        </div>
        <button type="submit" disabled={isPending}>
          {isPending ? 'Logging in...' : 'Login'}
        </button>
      </form>
      {message && <p style={{ color: message.startsWith('Login successful') ? 'green' : 'red' }}>{message}</p>}
       <p>Don't have an account? <a href="/register">Register here</a></p> {/* Link to register */}
    </div>
  );
}